import axios from 'axios'

export default {
  name: 'projectModule',
  data () {
    return {
      module: Array
    }
  },
  methods: {
    getModuleList () {
      axios.get(this.url + '/module/project/' + sessionStorage.getItem('projectId'))
        .then(function (res) {
          console.log(res + 'runmodule')
          console.log(res.data + 'runmodule.data')
          this.module = res.data
        })
    }
  },
  mounted () {
    this.getModuleList()
  }
}



