import axios from 'axios'

export default {
  name: 'newModule',
  props: {
    leader: Array
  },
  data () {
    return {
      module: {
        name: '',
        creator: '',
        start_time: '',
        end_time: '',
        content: '',
        leader: '',
        project_id: parseInt(sessionStorage.getItem('projectId')),
        participants: []
      }
    }
  },
  methods: {
    getStudentList () {
      axios.get(this.url + '/user/level/2')
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      console.log(res + 'userlevel2')
      res = res.data
      if (res) {
        this.StudentList = res
        console.log(this.StudentList + 'userlevel2.data')
      }
    },
    submit () {
      this.$axios({
        method: 'post',
        url: this.url + '/module/',
        data: JSON.stringify(this.module)
      }).then((module) => {
        console.log(module)
        alert('成功创建模块！')
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getStudentList()
  }
}



